<template>
    <div class="inviteToRoom">
        <div class="inviteLinksWrapper">
            <div class="inviteLinks">
                <label>Room link:</label>
                <b-input-group>
                    <b-form-input id="roomLinkInput" @click="copyRoomLink()"  :value="roomLink"></b-form-input>
                    <b-input-group-append>
                    <b-button @click="copyRoomLink()" variant="outline-secondary">Copy</b-button>
                    </b-input-group-append>
                </b-input-group>
            </div>
            <div class="inviteLinks">
                <label>Room Id:</label>
                <b-input-group>
                    <b-form-input id="roomIdInput" @click="copyRoomId()" :value="roomId"></b-form-input>
                    <b-input-group-append>
                    <b-button @click="copyRoomId()" variant="outline-secondary">Copy</b-button>
                    </b-input-group-append>
                </b-input-group>
            </div>
        </div>
        <b-button variant="primary" size="sm">Invite friends</b-button>
    </div>
</template>
<script>
export default {
    data:function(){
        return {
            roomId:this.$route.params.roomId,
            roomLink:window.location.origin+'/room/'+this.$route.params.roomId
        }
    },
    methods:{
        copyRoomLink(){
            var input = document.getElementById('roomLinkInput');
            input.select();
            document.execCommand('copy');
        },
        copyRoomId(){
            var input = document.getElementById('roomIdInput');
            input.select();
            document.execCommand('copy');
        }
    }
}
</script>